import { defineConfig } from 'sanity'
import { structureTool } from 'sanity/structure'
import { visionTool } from '@sanity/vision'
import { type StructureResolver } from 'sanity/structure'

import { schemaTypes } from './sanity/schemaTypes'
import { structure } from './sanity/structure'
import { apiVersion, dataset, projectId } from './sanity/env'
import { CustomLogo } from './sanity/components/CustomLogo'
import { CustomNavbar } from './sanity/components/CustomNavbar'
import { customDocumentActions } from './sanity/plugins/customDocumentActions'

const config = defineConfig({
  name: 'aries',
  title: 'Aries',
  basePath: '/cms',
  projectId,
  dataset,
  plugins: [
    structureTool({
      structure: structure as StructureResolver
    }),
    visionTool({ defaultApiVersion: apiVersion }),
    customDocumentActions(),
  ],
  schema: {
    types: schemaTypes,
  },
  document: {
    actions: (prev, { schemaType }) => {
      if (schemaType === 'product') {
        return prev
      }
      return prev
    }
  },
  studio: {
    components: {
      logo: CustomLogo,
      navbar: CustomNavbar,
    },
  },
})

export default config
