// sanity/plugins/customDocumentActions.ts
import { definePlugin, DocumentActionComponent } from 'sanity'

export const customDocumentActions = definePlugin(() => {
  return {
    name: 'custom-document-actions',
    document: {
      actions: (prev, context) => {
        if (context.schemaType === 'product') {
          const productActions: DocumentActionComponent[] = [
            // Add your custom product actions here
          ]
          return [...prev, ...productActions]
        }
        return prev
      }
    }
  }
})