import React from 'react'

export function SKUInput(props: StringInputProps) {
  const { value, onChange } = props
  const title = useFormValue(['title']) as string
  
  const generateSKU = () => {
    if (!title) return
    
    // Create SKU from title: uppercase, replace spaces with dashes, add random number
    const baseSlug = title
      .toUpperCase()
      .replace(/[^A-Z0-9]+/g, '-')
      .replace(/^-+|-+$/g, '')
    
    const randomNum = Math.floor(Math.random() * 10000)
      .toString()
      .padStart(4, '0')
    
    const newSKU = `${baseSlug}-${randomNum}`
    onChange(set(newSKU))
  }

  return (
    <div style={{ display: 'flex', gap: '1rem', alignItems: 'center' }}>
      <input
        type="text"
        value={value || ''}
        onChange={(event) => onChange(event.target.value ? set(event.target.value) : unset())}
        style={{
          padding: '0.5rem',
          border: '1px solid #ccc',
          borderRadius: '4px',
          flexGrow: 1,
        }}
      />
      <button
        type="button"
        onClick={generateSKU}
        style={{
          padding: '0.5rem 1rem',
          backgroundColor: '#00b3b0',
          color: 'white',
          border: 'none',
          borderRadius: '4px',
          cursor: 'pointer',
        }}
      >
        Generate SKU
      </button>
    </div>
  )
}
